      description: '<p>Go beyond the code, advance towards responsible and trustworthy AI. Begins March 27, 2020 and ends May 1, 2020. This is a free on-line webinar that can be accessed at any time.</p> <p>REGISTER HERE FOR THIS FREE COURSE &amp; EVENT: ibm.biz/AIExplainability</p> <p>Take our free masterclass and then apply your new skills in the online Hackathon. Come learn new open-source and IBM data science tools like AIX360 and Lale. As well as, how to explain your model to any stakeholder and how to integrate explainable workflows.</p> <p>MASTERCLASS<br/>Quickly learn new data science concepts with this free 90-minute micro-course.</p> <p>HACKATHON<br/>Apply these new concepts by creating your own project to showcase and compete for the $8,000 prizepool and an IBM skill badge.</p> <p>MENTORSHIP<br/>Connect 1:1 with IBM data science experts for help and guidance on your project.</p> <p>WHO IS THIS FOR?<br/>All Data Scientists, AI &amp; ML Devs and Engineers, Startups and Enterprise</p> <p>WHY EXPLAINABILITY?<br/>Explainability is the ability for data scientists to produce applicable results for their peers, managers, and end-users. Can your results be understood and applied by other data scientists and stakeholders? It is up to you as a data scientist to use your business knowledge, mathematical, and programmatic skills to communicate your findings and methods. Explainability is an important tool used to make your reasoning behind each decision transparent and repeatable.</p> <p>In this course, learn the importance of building an explainability workflow and how to implement these practices from the beginning. Then, using your new skills and tools, apply what you have learned by submitting your own project. Each submission for the hackathon will have a chance at cash prizes during the 6-week competition.<br/>-------------------------------------------------</p> <p>REGISTER HERE FOR THIS FREE COURSE &amp; EVENT: ibm.biz/AIExplainability</p> ',
