      description: '<p>Hallo liebe Kanbunnys,</p> <p>es ist wieder soweit: wir Kanbunnys treffen uns. Wir machen wieder einen Open Space und für Leute die Lust haben, gibt es die Möglichkeit getKanban zu spielen. Es ist eine Simulation die Limitierung von Arbeit, Priorisieren, gemeinsam Entscheiden, tracken von Metriken spielerisch vermittelt. Wir entscheiden zu Beginn des Open Space wer getKanban spielen will. Die Voraussetzung ist, dass sich mindestens 4 Leute finden die die ganze Zeit dabei sind.</p> <p>Neben getKanban habt ihr im Rahmen des Open Space die Möglichkeit eure eigenen Themen und Fragen zu platzieren.</p> <p>Ist es für Dich das erste Mal bei der Kanban Community: komm einfach vorbei, höre zu, bring Dich ein, wo du willst, und hab Spaß :)</p> <p>Für Pizza und Bier (natürlich auch Softdrinks) ist ab 18:30 gesorgt.</p> <p>Viele Grüße<br/>Sebastian, Chris und Oliver</p> ',
